// Base styles
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.dashboard-container {
  display: flex;
  height: 100vh;
  background-color: #f8fafc; // $gray-50
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

// Sidebar styles
.sidebar {
  width: 280px; // $sidebar-width
  background: #2563eb; // $primary-dark
  color: #e2e8f0; // $gray-200
  display: flex;
  flex-direction: column;
  position: fixed;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 1000;
  transition: transform 0.3s ease;

  .sidebar-header {
    padding: 1.5rem 1.25rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1); // $white with opacity

    .logo {
      display: flex;
      align-items: center;
      gap: 0.75rem;

      .logo-icon {
        width: 32px;
        height: 32px;
        background: #3b82f6; // $primary-color (primary-blue)
        border-radius: 8px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #ffffff; // $white

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }

      .logo-text {
        font-size: 1.25rem;
        font-weight: 700;
        color: #ffffff; // $white
      }
    }
  }

  .user-section {
    padding: 1rem 1.25rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1); // $white with opacity

    .user-info {
      display: flex;
      align-items: center;
      gap: 0.75rem;

      .user-avatar {
        width: 40px;
        height: 40px;
        background: rgba(59, 130, 246, 0.2); // $primary-color with opacity
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #3b82f6; // $primary-color (primary-blue)

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
        }
      }

      .user-details {
        .user-name {
          font-size: 0.875rem;
          font-weight: 600;
          color: #ffffff; // $white
          display: block;
        }

        .user-role {
          font-size: 0.75rem;
          color: #94a3b8; // $gray-400
          margin-top: 0.125rem;
        }
      }
    }
  }

  .nav-menu {
    flex: 1;
    padding: 1rem 0;
    overflow-y: auto;

    .nav-section {
      margin-bottom: 0.25rem;
    }

    .nav-item {
      display: flex;
      align-items: center;
      gap: 0.75rem;
      padding: 0.75rem 1.25rem;
      cursor: pointer;
      transition: all 0.2s ease;
      position: relative;
      font-size: 0.875rem;
      font-weight: 500;
      color: #cbd5e1; // $gray-300

      &:hover {
        background: rgba(255, 255, 255, 0.05); // $white with opacity
        color: #ffffff; // $white
      }

      &.active {
        background: rgba(59, 130, 246, 0.15); // $primary-color with opacity
        color: #ffffff; // $white
        border-right: 3px solid #3b82f6; // $primary-color (primary-blue)
      }

      &.expandable {
        .chevron {
          margin-left: auto;
          transition: transform 0.2s ease;
          font-size: 16px;
          width: 16px;
          height: 16px;

          &.expanded {
            transform: rotate(90deg);
          }
        }
      }

      &.logout {
        color: #f87171;

        &:hover {
          background: rgba(248, 113, 113, 0.1);
          color: #ef4444; // $error-color (red)
        }
      }

      .nav-icon {
        font-size: 18px;
        width: 18px;
        height: 18px;
        flex-shrink: 0;
      }

      .notification-dot {
        background: #ef4444; // $error-color (red)
        color: #ffffff; // $white
        font-size: 0.75rem;
        padding: 0.125rem 0.375rem;
        border-radius: 10px;
        min-width: 18px;
        text-align: center;
        margin-left: auto;
      }
    }

    .sub-menu {
      background: rgba(0, 0, 0, 0.2);

      .sub-item {
        display: flex;
        align-items: center;
        gap: 0.75rem;
        padding: 0.625rem 1.25rem 0.625rem 3.25rem;
        cursor: pointer;
        transition: all 0.2s ease;
        font-size: 0.875rem;
        color: #94a3b8; // $gray-400

        &:hover {
          background: rgba(255, 255, 255, 0.05); // $white with opacity
          color: #e2e8f0; // $gray-200
        }

        .sub-icon {
          font-size: 16px;
          width: 16px;
          height: 16px;
          flex-shrink: 0;
        }
      }
    }
  }

  .sidebar-footer {
    border-top: 1px solid rgba(255, 255, 255, 0.1); // $white with opacity
    padding: 1rem 0;
  }
}

// Main content styles
.main-content {
  flex: 1;
  margin-left: 280px; // $sidebar-width
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  .content-header {
    background: #ffffff; // $white
    padding: 1.5rem 2rem;
    border-bottom: 1px solid #e5e7eb;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;

    .header-left {
      h1 {
        font-size: 1.875rem;
        font-weight: 700;
        color: #1e293b; // $gray-800
        margin: 0;
      }

      p {
        color: #64748b; // $gray-500
        margin-top: 0.25rem;
        font-size: 0.875rem;
      }
    }

    .header-actions {
      display: flex;
      align-items: center;
      gap: 1rem;

      .btn-primary {
        background: #3b82f6; // $primary-color (primary-blue)
        color: #ffffff; // $white
        border: none;
        padding: 0.625rem 1.25rem;
        border-radius: 8px;
        font-size: 0.875rem;
        font-weight: 600;
        cursor: pointer;
        transition: all 0.2s ease;
        display: flex;
        align-items: center;
        gap: 0.5rem;

        &:hover {
          background: #2563eb; // $primary-dark
          transform: translateY(-1px);
          box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);
        }

        mat-icon {
          font-size: 16px;
          width: 16px;
          height: 16px;
        }
      }

      .notification-btn {
        position: relative;
        padding: 0.625rem;
        background: #f9fafb;
        border: 1px solid #e5e7eb;
        border-radius: 8px;
        cursor: pointer;
        transition: all 0.2s ease;

        &:hover {
          background: #f3f4f6;
        }

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
          color: #64748b; // $gray-500
        }

        .notification-badge {
          position: absolute;
          top: -2px;
          right: -2px;
          background: #ef4444; // $error-color (red)
          color: #ffffff; // $white
          font-size: 0.75rem;
          padding: 0.125rem 0.375rem;
          border-radius: 10px;
          min-width: 18px;
          text-align: center;
        }
      }
    }
  }

  .dashboard-content {
    flex: 1;
    padding: 2rem;
    background: #f8fafc; // $gray-50

    .stats-grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
      gap: 1.5rem;
      margin-bottom: 2rem;

      .stat-card {
        background: #ffffff; // $white
        padding: 1.5rem;
        border-radius: 12px;
        border: 1px solid #e5e7eb;
        transition: all 0.2s ease;

        &:hover {
          box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
          transform: translateY(-2px);
        }

        .stat-header {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 1rem;

          .stat-title {
            font-size: 0.875rem;
            color: #64748b; // $gray-500
            font-weight: 500;
          }

          .stat-icon {
            width: 40px;
            height: 40px;
            border-radius: 10px;
            display: flex;
            align-items: center;
            justify-content: center;

            mat-icon {
              font-size: 20px;
              width: 20px;
              height: 20px;
            }

            &.primary {
              background: #dbeafe;
              color: #3b82f6; // $primary-color (primary-blue)
            }

            &.success {
              background: #dcfce7;
              color: #10b981; // $success-color (green)
            }

            &.warning {
              background: #fef3c7;
              color: #f59e0b; // $warning-color (orange)
            }

            &.danger {
              background: #fee2e2;
              color: #ef4444; // $error-color (red)
            }
          }
        }

        .stat-value {
          font-size: 2rem;
          font-weight: 700;
          color: #1e293b; // $gray-800
          margin-bottom: 0.5rem;
        }

        .stat-change {
          display: flex;
          align-items: center;
          gap: 0.25rem;
          font-size: 0.875rem;

          &.positive {
            color: #10b981; // $success-color (green)
          }

          &.negative {
            color: #ef4444; // $error-color (red)
          }

          mat-icon {
            font-size: 14px;
            width: 14px;
            height: 14px;
          }
        }
      }
    }

    .content-grid {
      display: grid;
      grid-template-columns: 2fr 1fr;
      gap: 1.5rem;

      .content-card {
        background: #ffffff; // $white
        border-radius: 12px;
        border: 1px solid #e5e7eb;
        overflow: hidden;

        .card-header {
          padding: 1.5rem 1.5rem 0;

          h3 {
            font-size: 1.125rem;
            font-weight: 600;
            color: #1e293b; // $gray-800
            margin: 0;
          }

          p {
            color: #64748b; // $gray-500
            font-size: 0.875rem;
            margin-top: 0.25rem;
          }
        }

        .card-content {
          padding: 1.5rem;

          .activity-list {
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .activity-item {
              display: flex;
              gap: 0.75rem;
              padding: 0.75rem;
              border-radius: 8px;
              transition: background-color 0.2s ease;

              &:hover {
                background: #f9fafb;
              }

              .activity-icon {
                width: 40px;
                height: 40px;
                border-radius: 8px;
                background: #f3f4f6;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-shrink: 0;

                mat-icon {
                  font-size: 18px;
                  width: 18px;
                  height: 18px;
                  color: #64748b; // $gray-500
                }
              }

              .activity-details {
                flex: 1;

                .activity-text {
                  font-size: 0.875rem;
                  color: #1e293b; // $gray-800
                  margin-bottom: 0.25rem;
                  font-weight: 500;
                }

                .activity-time {
                  font-size: 0.75rem;
                  color: #64748b; // $gray-500
                }
              }
            }
          }

          .quick-stats {
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .quick-stat {
              display: flex;
              align-items: center;
              gap: 0.75rem;
              padding: 1rem;
              border-radius: 8px;
              background: #f9fafb;

              .quick-stat-icon {
                width: 36px;
                height: 36px;
                border-radius: 8px;
                background: #ffffff; // $white
                display: flex;
                align-items: center;
                justify-content: center;

                mat-icon {
                  font-size: 18px;
                  width: 18px;
                  height: 18px;
                  color: #3b82f6; // $primary-color (primary-blue)
                }
              }

              .quick-stat-info {
                .stat-amount {
                  font-size: 1.125rem;
                  font-weight: 700;
                  color: #1e293b; // $gray-800
                }

                .stat-label {
                  font-size: 0.75rem;
                  color: #64748b; // $gray-500
                  margin-top: 0.125rem;
                }
              }
            }
          }
        }
      }
    }
  }
}

// Mobile menu styles
.mobile-menu-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;

  &.active {
    opacity: 1;
    pointer-events: auto;
  }
}

.mobile-menu-btn {
  display: none;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: #ffffff; // $white
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  cursor: pointer;
  position: fixed;
  top: 1rem;
  left: 1rem;
  z-index: 1001;

  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
    color: #64748b; // $gray-500
  }
}

// Responsive design
@media (max-width: 1024px) {
  .main-content .dashboard-content .content-grid {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .mobile-menu-overlay {
    display: block;
  }

  .mobile-menu-btn {
    display: flex;
  }

  .sidebar {
    width: 280px; // $sidebar-mobile-width
    transform: translateX(-100%);

    &.mobile-open {
      transform: translateX(0);
    }
  }
  
  .main-content {
    margin-left: 0;

    .content-header {
      padding: 1rem 1.5rem;

      .header-left h1 {
        font-size: 1.5rem;
      }
    }

    .dashboard-content {
      padding: 1.5rem;

      .stats-grid {
        grid-template-columns: 1fr;
      }
    }
  }
}

@media (max-width: 640px) {
  .main-content .content-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;

    .header-actions {
      width: 100%;
      justify-content: space-between;

      .btn-primary {
        flex: 1;
        justify-content: center;
      }
    }
  }
}